import { HEAD_CELLS } from '../constantas/data-generator-for';

const dataToCSVPreparation = (data, config = { columnConfig: HEAD_CELLS }) => {
  const newArr = [];
  data.forEach(
    ({
      index,
      id,
      company,
      url,
      foundedYear,
      founder,
      fundingTotal,
      status,
      geo,
      businessTypes,
    }) => {
      let newEl = { id };

      if (config.columnConfig[0].isVisible) {
        newEl = { ...newEl, index };
      }
      if (config.columnConfig[2].isVisible) {
        newEl = { ...newEl, company, url };
      }
      if (config.columnConfig[3].isVisible) {
        newEl = { ...newEl, foundedYear };
      }
      if (config.columnConfig[4].isVisible) {
        newEl = { ...newEl, founder };
      }
      if (config.columnConfig[5].isVisible) {
        newEl = { ...newEl, fundingTotal };
      }
      if (config.columnConfig[6].isVisible) {
        const categories = businessTypes.reduce(
          (accumulator, currentValue) => `${accumulator} ${currentValue},`,
          ''
        );
        newEl = { ...newEl, categories };
      }
      if (config.columnConfig[7].isVisible) {
        newEl = { ...newEl, status };
      }
      if (config.columnConfig[8].isVisible) {
        const num = n =>
          n.toLocaleString('ru-RU', {
            maximumFractionDigits: 2,
            minimumFractionDigits: 2,
          });
        newEl = { ...newEl, latitude: num(geo.latitude), longitude: num(geo.longitude) };
      }

      newArr.push(newEl);
    }
  );
  return newArr;
};

export default dataToCSVPreparation;
